import React from "react";
import './App.css'; // Importação do arquivo CSS
import AliceCarousel from 'react-alice-carousel'; // Importando o Alice Carousel
import 'react-alice-carousel/lib/alice-carousel.css'; // Importando o estilo do Alice Carousel
import Header from "./components/Header";
import Menu from "./components/Menu";

function App() {
  // Itens do carrossel
  const items = [
    <img
      src={`${process.env.PUBLIC_URL}/fotomeio.jpg`}
      alt="Foto 1"
      className="carousel-image"
      key="foto1"
    />,
    <img
      src={`${process.env.PUBLIC_URL}/foto2.jpg`}
      alt="Foto 2"
      className="carousel-image"
      key="foto2"
    />,
    <img
      src={`${process.env.PUBLIC_URL}/foto3.jpg`}
      alt="Foto 3"
      className="carousel-image"
      key="foto3"
    />,
    // Adicione mais imagens conforme necessário
  ];

  return (
    <div className="app-container">
      <Header />
      <Menu />
      <main>
        <h1>B E M - V I N D O S(AS)!</h1>
        <p>Site do Coletivo Literário Kalúnia.</p>

        {/* Carrossel de Imagens usando o Alice Carousel */}
        <AliceCarousel
        infinite={true} // Faz com que o carrossel volte ao início após o último slide
        mouseDragEnabled={true} // Permite o arrasto do mouse para navegar entre as imagens
        items={items} // Passando os itens (imagens)
        className="alice-carousel" // Aplica uma classe para customizar a aparência
        />
      </main>
    </div>
  );
}

export default App;
